<template>
<div class="project-box">
    <div class="project-image">
        <div class="slide" ref="slide" v-for="(img, index) in img_src" :key="index">
            <img :src="`${img}`" />
        </div>
    </div>
    <div class="dots">
        <span class="dot" v-for="index in img_src.length" :key="index" @click="changeSlide()"></span>
    </div>
    <div class="project-infos">
        <div class="project-title">
            {{ title }}
        </div>
        <div class="project-category">
            {{ category }}
        </div>
        <div class="project-desc">
            <div>{{ desc }}</div>
        </div>
        <div class="project-tech-used">
            <div class="tech-items" v-for="(tech, index) in techs" :key="index">#{{ tech }}</div>
        </div>
        <div class="project-see-more">
            <router-link :to="`/project/${title}`"></router-link>
        </div>
    </div>
</div>
</template>
<script>
import right_svg from '@/assets/svg/right.svg';
export default{
    name: 'ProjectBox',
    props: {
        img_src: Array,
        title: String,
        category: String,
        desc: String,
        techs: Array,

    }, 
    data() {
        return {
            index: 0,
            right_svg: right_svg,
        }
    },
    mounted() {
        this.initialSlides();
        this.changeSlide();
    },
    methods: {
        initialSlides(){
            var slides = this.$el.querySelectorAll(".slide");
            slides[this.index].style.display = 'block';
        },
        changeSlide(){
            var slides = this.$el.querySelectorAll(".slide");
            var dots = this.$el.querySelectorAll(".dot");
            if(this.index > slides.length - 1){
                this.index = 0;
            }
            if(this.index < 0){
                this.index = slides.length - 1
            }
            for(let i = 0; i < slides.length; i++){
                slides[i].style.display = 'none';
                dots[i].classList.remove("active");
            }
            slides[this.index].style.display = 'block';
            dots[this.index].classList.add("active");
            this.index++;

            setTimeout(() => {
                this.changeSlide();
            }, 3000);
        
        }
        
    }

}
</script>
